import React from "react";

import { Button, DialogContentText } from "@material-ui/core";
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import InfoDialog from "../Dialog/InfoDialog";
import AddNew from "../Form/AddNewForm";

export default function FigureCard(props: any) {
    const createEditComponent = (formData: any) => {
        return <AddNew {...props} data={formData} />
    }

    const deleteItem = () => {
        alert('deleting')
    }
    const createDeleteComponent = () => {
        return <div className="text-center">
            <DialogContentText className="text-left">
                Are you sure you want to delete {props.title}
                {/* <Error errorMessage={errorMessage} /> */}
            </DialogContentText>
            <Button
                variant="outlined"
                onClick={() => { deleteItem() }}
                color="secondary"
                autoFocus>
                Yes
            </Button>
        </div>
    }

    return (
        <figure className="figure">
            {props.figure ? <img src={props.url} className="figure-img img-fluid z-depth-1"
                alt="" style={{ width: '200px', margin: '10px' }} /> :
                <iframe title="video" src={props.url} allowFullScreen></iframe>}
            <figcaption className="figure-caption text-center">
                {props.title}
                <br />
                <InfoDialog
                    isIconButton={true}
                    iconButton={<EditIcon color="primary" />}
                    btnLabel="Edit"
                    btnVariant="contained"
                    title={`Edit ${props.title || ''}`}
                    component={createEditComponent(props)}
                />
                <InfoDialog
                    isIconButton={true}
                    iconButton={<DeleteIcon color="error" />}
                    btnLabel="Delete"
                    btnVariant="contained"
                    title={`Delete ${props._id || ''}`}
                    component={createDeleteComponent()}
                />
            </figcaption>
        </figure>
    );
}