import { MDBContainer } from 'mdbreact'
import React from 'react'
import MediaCard from '../components/Media/MediaCard'
import CloudUploadIcon from '@material-ui/icons/CloudUpload'
import { MDBNavbar, MDBNavbarBrand, MDBNavbarNav, MDBCollapse, MDBNavItem, MDBNavLink } from 'mdbreact';
import { BrowserRouter as Router } from 'react-router-dom'
import AddNew from '../components/Form/AddNewForm'
import InfoDialog from '../components/Dialog/InfoDialog'
import { ADD_IMAGE_FORM, ADD_VIDEO_FORM } from '../services/Form'

export default function AdminPage() {
    const imageList = [{
        title: 'Image Name 1',
        url: 'https://mdbootstrap.com/img/Photos/Horizontal/Nature/8-col/img%20(73).jpg',
        description: 'Image Description'
    }, {
        title: 'Image Name 2',
        url: 'https://www.news-medical.net/image.axd?picture=2018%2F4%2Fshutterstock_By_ESB_Professional.jpg',
        description: 'Image Description'
    }]

    const videoList = [{
        title: 'Video Name 1',
        url: 'https://www.youtube.com/embed/v64KOxKVLVg',
        description: 'Video Description'
    }, {
        title: 'Video Name 2',
        url: 'https://player.vimeo.com/video/137857207',
        description: 'Video Description'
    }]

    const createImageCard = () => {
        return imageList.map(img => <MediaCard figure {...img} form={ADD_IMAGE_FORM} />)
    }

    const createVideoCard = () => {
        return videoList.map(video => <MediaCard {...video} form={ADD_VIDEO_FORM} />)
    }

    return <>
        <Router>
            <MDBNavbar fixed="top" expand="md" scrolling className="p-0 shadow-none">
                <MDBNavbarBrand href="/">
                    <img alt="app logo" height="50px" src={require('../assets/icons/app_logo.png')} />
                </MDBNavbarBrand>
                <MDBCollapse navbar>
                    <MDBNavbarNav right>
                        <MDBNavItem active>
                            <MDBNavLink className="text-dark" to="/">Website</MDBNavLink>
                        </MDBNavItem>
                        <MDBNavItem>
                            <MDBNavLink className="text-dark" to="#">Profile</MDBNavLink>
                        </MDBNavItem>
                        <MDBNavItem className="align-right">
                            <MDBNavLink className="text-dark" to="#">Logout</MDBNavLink>
                        </MDBNavItem>
                    </MDBNavbarNav>
                </MDBCollapse>
            </MDBNavbar>
        </Router>

        <MDBContainer className="mt-5">
            <h3 className="mt-2">Image Gallery</h3>
            {createImageCard()}
            <br />
            <InfoDialog
                iconButton={<CloudUploadIcon color="primary" />}
                btnLabel="Add New Image"
                btnVariant="contained"
                title={`Add New Image`}
                component={<AddNew form={ADD_IMAGE_FORM} />}
            />
            <h3 className="mt-2">Video Gallery</h3>
            {createVideoCard()}
            <br />
            <InfoDialog
                iconButton={<CloudUploadIcon color="primary" />}
                btnLabel="Add New Video"
                btnVariant="contained"
                title={`Add New Video`}
                component={<AddNew form={ADD_VIDEO_FORM} />}
            />
            <h3 className="mt-2">Contact Details</h3>
            {/* <h4>Phone Number</h4>
            <MDBInput small label="Contact" icon="phone" />
            <h4>Email Id</h4>
            <MDBInput label="Email" icon="email" />
            <h4>Address</h4> */}
        </MDBContainer>
    </>
}