import React from "react";
import { MDBCol, MDBContainer, MDBRow, MDBFooter, MDBIcon } from "mdbreact";
import { COLORS, CSS } from "../../assets/style";

const Footer = () => {
    return (
        <MDBFooter style={{ background: "#232323" }} className="font-small pt-5 mt-5">
            <MDBContainer className="text-center text-md-left">
                <MDBRow className="text-center">
                    <MDBCol md="6">
                        <h5 style={{ ...CSS.header }} className="title">Footer Content</h5>
                        <p style={{ ...CSS.smallHeader }}>
                            Here you can use rows and columns here to organize your footer
                            content.
                    </p>
                    </MDBCol>
                    <MDBCol md="3">
                        <h5 className="title" style={{ ...CSS.header }}>Links</h5>
                        <ul className="p-0">
                            <li className="list-unstyled"><a href="#!">About</a></li>
                            <li className="list-unstyled"><a href="#!">Service</a></li>
                            <li className="list-unstyled"><a href="#!">Plan</a></li>
                        </ul>
                    </MDBCol>
                    <MDBCol md="3">
                        <h5 className="title" style={{ ...CSS.header }}>Contact Us</h5>
                        <ul className="text-lg-left list-unstyled ml-4">
                            <li><MDBIcon icon="map-marker-alt" className="pr-2" />New York, 94126 USA</li>
                            <li><MDBIcon icon="phone" className="pr-2" />+ 01 234 567 89</li>
                            <li><MDBIcon icon="envelope" className="pr-2" />contact@example.com
                            </li>
                        </ul>
                        <hr className="hr-light my-4" />
                        <ul className="list-inline text-center list-unstyled">
                            <li className="list-inline-item">
                                <a href="#!" className="p-2 fa-lg w-ic">
                                    <MDBIcon fab icon="twitter" />
                                </a>
                            </li>
                            <li className="list-inline-item">
                                <a href="#!" className="p-2 fa-lg w-ic">
                                    <MDBIcon fab icon="linkedin-in" />
                                </a>
                            </li>
                            <li className="list-inline-item">
                                <a href="#!" className="p-2 fa-lg w-ic">
                                    <MDBIcon fab icon="instagram" />
                                </a>
                            </li>
                        </ul>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
            <div className="footer-copyright text-center py-3">
                <MDBContainer fluid>
                    &copy; {new Date().getFullYear()} Copyright: <a href="https://www.theworkboxsolutions.com"> theworkboxsolutions.com </a>
                </MDBContainer>
            </div>
        </MDBFooter>
    );
}

export default Footer;