import React from 'react';
import Button from '@material-ui/core/Button';
import { MDBCard, MDBCardBody, MDBCardImage, MDBCardTitle, MDBCardText, MDBRow, MDBCol, MDBView, MDBIcon } from 'mdbreact';
import { CSS } from "../../assets/style";

const BasicCard = () => {
    const CardList = [{
        title: "Nutritional Therapy",
        specialInfo: "Best seller",
        description: "Book Your 1 on 1 consultation here.",
        btnLabel: "View Pricing",
        imgUrl: "https://sa1s3optim.patientpop.com/assets/images/provider/photos/2097763.jpg",
        redirectUrl: "#"
    },
    {
        title: "Lowe Back Pain",
        specialInfo: "Best seller",
        description: "Book Your 1 on 1 consultation here.",
        btnLabel: "View Pricing",
        imgUrl: "https://www.impactphysicaltherapy.com/wp-content/uploads/2018/07/Understanding-Lower-Back-Pain-Causes.png",
        redirectUrl: "#"
    },
    {
        title: "Neck Pain",
        specialInfo: null,
        description: "Book Your 1 on 1 consultation here.",
        btnLabel: "View Pricing",
        imgUrl: "https://www.spineuniverse.com/sites/default/files/imagecache/gallery-large/wysiwyg_imageupload/48608/2019/07/25/Painful%20Neck%20123RF.jpg",
        redirectUrl: "#"
    }]

    return (
        <MDBRow>
            {CardList.map(listItem => <MDBCol md='4'>
                <MDBCard narrow>
                    <MDBView cascade>
                        <MDBCardImage
                            hover
                            overlay='white-slight'
                            className='card-img-top'
                            src={listItem.imgUrl}
                            alt='food'
                        />
                    </MDBView>

                    <MDBCardBody>
                        <h5 className={`${!listItem.specialInfo ? 'invisible ' : ''} pink-text`} >
                            <MDBIcon icon='fas fa-hand-holding-heart' /> {listItem.specialInfo}
                        </h5>

                        <MDBCardTitle className='font-weight-bold'>
                            {listItem.title}
                        </MDBCardTitle>

                        <MDBCardText>
                            {listItem.description}
                        </MDBCardText>

                        <div className="text-center">
                            <Button style={{ ...CSS.btn }}>{listItem.btnLabel}</Button>
                        </div>
                    </MDBCardBody>
                </MDBCard>
            </MDBCol>)
            }
        </MDBRow >
    )
}

export default BasicCard;